
* {
  margin: 0;
  padding: 0;
}
html,
body {
  width: 100%;
  height: 100%;
}
textarea.ivu-input {
  height: 100% !important;
  background-color: #f6f7f9;
  border: none;
  font-size: 18px;
}
textarea.ivu-input:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
}
.task-list {
  list-style: none;
}
#app {
  color: #2c3e50;
  height: 100%;
}
#app .g_editor_container {
  height: calc(100% - 56px);
}
#app .editor,
#app .preview {
  width: 50%;
  height: 100%;
  padding: 5px 0 0 15px;
  background: #f6f7f9;
  float: left;
  overflow: auto;
  font-size: 18px;
}
#app .editor::before,
#app .preview::before {
  content: "\7F16\7801\533A";
  font-size: 20px;
  font-weight: 800;
}
#app .editor .textarea,
#app .preview .textarea {
  height: calc(100% - 40px);
  background-color: #f6f7f9;
}
#app .preview {
  background: #fff;
  padding-left: 30px;
  font-size: 15px;
  font-weight: 400;
}
#app .preview::before {
  content: "\9884\89C8\533A";
}
#app .preview img[alt="img_preview"] {
  max-width: 100%;
  max-height: 600px;
}

.header[data-v-6e6e6fc6] {
  height: 56px;
  line-height: 56px;
  border-bottom: 1px #ccc solid;
  -webkit-box-shadow: 2px 2px 2px #ccc;
          box-shadow: 2px 2px 2px #ccc;
}
.header .center[data-v-6e6e6fc6] {
  text-align: center;
}
.header .m_action_item[data-v-6e6e6fc6] {
  cursor: pointer;
}
.header .m_action_item[data-v-6e6e6fc6]:hover {
  color: #0b9bd4;
  font-weight: 900;
}
